import { createStore, applyMiddleware } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';
import thunk from 'redux-thunk';
import rootReducer from './redux';
// import setAuthToken from "./redux/utils/setAuthToken";
import setAuthToken from './redux/utils/setAuthToken';
const initialState = {};

const middleware = [thunk];
const store = createStore(rootReducer, initialState, composeWithDevTools(applyMiddleware(...middleware)));

let currentState = store.getState();

store.subscribe(() => {
    let previousState = currentState;
    currentState = store.getState();
    if (previousState.auth.jwtToken !== currentState.auth.jwtToken) {
        const token = currentState.auth.jwtToken;

        const id = currentState.auth.id;
        console.log('currentState.auth');
        console.log(currentState.auth);
        setAuthToken(token, id);
    }
    //   console.log("my current state :", currentState);
});

export default store;
