import React, { useState } from "react";
import { Tab, Nav, Button, Modal } from "react-bootstrap";
import Conversations from "./Conversations";
import Contacts from "./ContactsOLD";
import NewContactModal from "./NewContactModal";
import NewConversationModal from "./NewConversationModal";
import { useConversations } from "../contexts/ConversationsProvider";
import { Media } from "reactstrap";
var  ChattingWith = localStorage.getItem('ChattingWith');
const CONVERSATIONS_KEY = "conversations";
const CONTACTS_KEY = "contacts";

export default function Sidebar({ id }) {
  const [activeKey, setActiveKey] = useState(CONVERSATIONS_KEY);
  const [modalOpen, setModalOpen] = useState(false);
  const { conversations } = useConversations();
  const conversationsOpen = activeKey === CONVERSATIONS_KEY;

  function closeModal() {
    setModalOpen(false);
  }
  console.log("conversations from side bar");
  console.log(conversations);

  return (
    <div style={{ width: "250px" }} className='d-flex flex-column'>
      <Tab.Container activeKey={activeKey} onSelect={setActiveKey}>
        <Nav variant='tabs' className='justify-content-center'>
          <Nav.Item>
            {/* <Nav.Link eventKey={CONVERSATIONS_KEY}>Conversations</Nav.Link> */}
          </Nav.Item>
          <Nav.Item>
            {/* <Nav.Link eventKey={CONTACTS_KEY}>Contacts</Nav.Link> */}
          </Nav.Item>
        </Nav>
        <Tab.Content className='border-right overflow-auto flex-grow-1'>

          <Tab.Pane eventKey={CONVERSATIONS_KEY}>{conversations ? <Conversations /> : "no conn"}</Tab.Pane>
          <Tab.Pane eventKey={CONTACTS_KEY}>
            <Contacts />
          </Tab.Pane>
        </Tab.Content>

        <div className='p-2 border-top border-right small'>

        <Media left>
                                <Media
                                    object
                                    src={'https://images.unsplash.com/photo-1511367461989-f85a21fda167?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&w=200&q=80'}
                                    alt="user profile"
                                    width="50px"
                                    style={{ marginTop: '10px' }}
                                    height="40px"
                                    className="rounded-circle"
                                />
                            </Media>
          <h4 className='text-muted'>  {  ChattingWith}</h4>
          <br></br>
          <br></br>

          
        </div>
        {/* <Button onClick={() => setModalOpen(true)} className='rounded-0'>
          New {conversationsOpen ? "Conversation" : "Contact"}
        </Button> */}
      </Tab.Container>

      <Modal show={modalOpen} onHide={closeModal}>
        {conversationsOpen ? (
          <NewConversationModal closeModal={closeModal} />
        ) : (
          <NewContactModal closeModal={closeModal} />
        )}
      </Modal>
    </div>
  );
}