
import React from "react";
import { useState } from "react";
import { ListGroup,Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import { useConversations } from "../contexts/ConversationsProvider";
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
import { connect } from "rxjs";
var ChattingWith 
export default function Conversations() {
  const { conversations, Chatting,FuncDeleteConversation } = useConversations();

    // const[ChattingWith,setChattingWith]=    useState('no')
    ChattingWith = localStorage.getItem('ChattingWith');
  const submit = (ConnectedUserID) => {
    confirmAlert({
      title: 'Confirm to delete conversation',
      message: 'Are you sure to delete the conversation ?.',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {FuncDeleteConversation({ConnectedUserID:ConnectedUserID})}
        },
        {
          label: 'No',
          // onClick: () => alert('Click No')
        }
      ]
    })
  };



  return (
    <ListGroup variant='flush'>

      {conversations[0]
        ? conversations.map ( (conversation, index) => (
          <div>
            <Link  to={"/chat/" + conversation.ConnectedUserID}>
              <ListGroup.Item style={{width:"100px" ,float:"left"}}
                onClick={() => {
                  localStorage.setItem('ChattingWith',conversation.firstname )
                  Chatting(conversation.ConnectedUserID);
                  setTimeout(() => {
               
                    window.location.reload(false);
                  }, 500);
                  // localStorage.setItem('ChattingWith','no' )
 ChattingWith = localStorage.getItem('ChattingWith');
                }}
                key={index}
                action>
                {conversation.firstname}
               
                
                {/* <div className="w3-padding w3-xlarge w3-teal"> */}
    
   
   
  {/* </div> */}

              </ListGroup.Item>
            </Link>
          <div>
           <span onClick={()=>{submit(conversation.ConnectedUserID)}} style={{float:"right", marginTop:"20px", marginRight:"20px"}} className="w3-xxlarge fa fa-trash"  >

             {/* <i className="w3-xxlarge fa fa-trash"></i> */}
           </span>
</div>

           <div
                  style={{

                    paddingTop: "4px",
                    paddingBottom: "4px",
                    paddingRight: "7px",
                    paddingLeft: "7px",
                    backgroundColor: "red",
                    color: "white",
                  
                    top: "6px",
                    right: "20px",
                    fontSize: ".7em",
                    borderRadius: "30px",
                    marginTop: "5px",
                   
                    float:"right"
                  }}
                  className='badge alert'>
                  {(conversation.notRead>0)?conversation.notRead:''}
                </div>
          


             </div>
          ))
        : "no con"}
    
    </ListGroup>
   
  );
}
