// External components
import React, { useState, useEffect } from 'react';
import { Container, Row, Col } from 'reactstrap';
import { Sticky } from 'react-sticky';
import { Navbar, Nav, NavItem } from 'reactstrap';
import { Link, NavLink } from 'react-router-dom';
import classnames from 'classnames';
// Own Components
import ButtonGroups from './ButtonGroups';
import Logo from './Logo';
import Navigation from './Navigation';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
const Header = () => {

    const token = localStorage.getItem('token');
    const [visible, setVisible] = useState(false);
    const resetNavBoc = () => setVisible(false);

    return token ? (
        <Sticky disableCompensation topOffset={400}>
            {/* <> */}
            {({ style, isSticky }) => (
                <header
                    style={style}
                    className={` ${isSticky ? 'sticky-header' : 'position-relative border bg-white'}`}>
                    <Container fluid>
                        <Row className='align-items-center '>
                            <Col xl={2} lg={2} md={2} sm={2} xs={2} className='d-flex'>
                                <div className='jb-logo'>
                                    <Logo />
                                </div>
                            </Col>
                            <Col className='nav-col' xl={8} lg={9} md={5} sm={7} xs={5}>
                                <Navigation />
                            </Col>
                            <Col className='p-0' xl={2} lg={1} md={5} sm={3} xs={5}>
                                <div className='user-info d-flex justify-content-center align-items-center align-self-center text-center'>
                                    <ButtonGroups />
                                </div>
                            </Col>
                        </Row>
                    </Container>
                </header>
            )}
            {/* </> */}
        </Sticky>
    ) : (
        <Sticky disableCompensation topOffset={400}>
            {({ style, isSticky }) => (
                <header
                    style={style}
                    className={` ${isSticky ? 'sticky-header' : 'position-relative border bg-white'}`}>
                    <Container fluid>
                        <Row className='align-items-center '>
                            <Col xl={2} lg={2} md={2} sm={2} xs={2} className='d-flex'>
                                <div className='jb-logo'>
                                    <Logo />
                                </div>
                            </Col>
                            <Col className='nav-col' xl={8} lg={9} md={5} sm={7} xs={5}>
                                <Navigation />
                            </Col>
                            <div
                                className={classnames('navigation position-relative', {
                                    'menu-container-visable': visible,
                                })}>
                                <Navbar>
                                    <Nav>
                                        <NavItem className='dropdown'>
                                            <NavLink
                                                className='nav-link position-relative main-nav text-dark'
                                                to='/signin'
                                                onMouseEnter={resetNavBoc}>
                                                Login
                                            </NavLink>
                                        </NavItem>

                                        <NavItem className='dropdown'>
                                            <NavLink
                                                onMouseEnter={resetNavBoc}
                                                className='nav-link position-relative main-nav text-dark'
                                                to='/account/register'>
                                                Register
                                            </NavLink>
                                        </NavItem>
                                    </Nav>
                                </Navbar>
                            </div>
                        </Row>
                    </Container>
                </header>
            )}
        </Sticky>
    );
};

Header.propTypes = {
    //   user: PropTypes.object.isRequired,
    isAuthenticated: PropTypes.bool,
};

const mapStateToProps = (state) => ({
    isAuthenticated: state.auth.isAuthenticated,
});

export default connect(mapStateToProps, {})(Header);
