import React, { Suspense, lazy, useEffect, useState } from 'react';
import { BrowserRouter as Router, Switch, Route, withRouter, useLocation, Redirect } from 'react-router-dom';
import { StickyContainer } from 'react-sticky';
import 'src/assets/scss/style.scss';
import Newsletter from 'src/components/Newsletter';
import Fallback from 'src/components/Fallback';
import Header from 'src/components/Header';
import Footer from 'src/components/Footer';
import withTitle from 'src/components/TitleComponent';
import Chat from 'src/Chat/components/Chat';
import { Nav2 } from '../components/Nav';
import { accountService } from '../services/account.service';
import { Account } from '../account/Index';
import store from '../../store';
import axios from 'axios';
import { ForgotPassword } from '../../views/account/ForgotPassword';

import onSubmit from '../Signin'

const LayoutOne = lazy(() => import('../LayoutOne'));
const Blog = lazy(() => import('../Blog'));
const Candidate = lazy(() => import('../Candidate'));
const Employer = lazy(() => import('../Employer'));
const About = lazy(() => import('../About'));
const ContactUs = lazy(() => import('../ContactUs'));
const NotFound = lazy(() => import('../NotFound'));
const Pricing = lazy(() => import('../Pricing'));
const Company = lazy(() => import('../Company'));
const CompanyDetails = lazy(() => import('../CompanyDetails'));
const Job = lazy(() => import('../Job'));
// const EditJobDetails = lazy(() => import('../Candidate/EditPost'));
const JobDetails = lazy(() => import('../JobDetails'));
const Signup = lazy(() => import('../SignUp'));
const Signin = lazy(() => import('../Signin'));

const App = () => {
    const { pathname } = useLocation();

    return (
        <Router>
            <StickyContainer>
                <Header />
                <Suspense fallback={<Fallback />}>
                    <Switch>
                        <Redirect from='/:url*(/+)' to={pathname.slice(0, -1)} />

                        <Route path='/account' component={Account} />

                        <Route
                            exact
                            path='/'
                            render={(props) =>
                                withTitle({
                                    component: LayoutOne,
                                    ...props,
                                })
                            }
                        />

                        <Route
                            exact
                            path='/contactus'
                            render={(props) =>
                                withTitle({
                                    component: ContactUs,
                                    title: 'Contact us',
                                    ...props,
                                })
                            }
                        />

                        <Route
                            exact
                            path='/about'
                            render={(props) =>
                                withTitle({
                                    component: About,
                                    title: 'About us',
                                    ...props,
                                })
                            }
                        />

                        <Route
                            exact
                            path='/pricing'
                            render={(props) =>
                                withTitle({
                                    component: Pricing,
                                    title: 'Pricing plan',
                                    ...props,
                                })
                            }
                        />

                        <Route
                            exact
                            path='/company'
                            render={(props) =>
                                withTitle({
                                    component: Company,
                                    title: 'Company',
                                    ...props,
                                })
                            }
                        />

                        <Route
                            exact
                            path='/company/:userID'
                            render={(props) =>
                                withTitle({
                                    component: CompanyDetails,
                                    title: 'Company details',
                                    ...props,
                                })
                            }
                        />

                        <Route
                            exact
                            path='/job'
                            render={(props) =>
                                withTitle({
                                    component: Job,
                                    title: 'Projects',
                                    ...props,
                                })
                            }
                        />

                        <Route
                            exact
                            path='/job/:serviceID'
                            render={(props) =>
                                withTitle({
                                    component: JobDetails,
                                    title: 'Project details',
                                    ...props,
                                })
                            }
                        />
                        {/* <Route
                            exact
                            path='/edit/:serviceID'
                            render={(props) =>
                                withTitle({
                                    component: EditJobDetails,
                                    title: 'Edit Project',
                                    ...props,
                                })
                            }
                        /> */}

                        <Route
                            exact
                            path='/signin'
                            render={(props) =>
                                withTitle({
                                    component: Signin,
                                    title: 'Login',
                                    ...props,
                                })
                            }
                        />

                        <Route
                            exact
                            path='/signup'
                            render={(props) =>
                                withTitle({
                                    component: Signup,
                                    title: 'Sign up',
                                    ...props,
                                })
                            }
                        />

                        <Route
                            exact
                            path='/forget-Password'
                            render={() =>
                                withTitle({
                                    component: ForgotPassword,
                                    title: 'Forgot password',
                                })
                            }
                        />

                        <Route
                            exact
                            path='/chat/:id'
                            render={(props) =>
                                withTitle({
                                    component: Chat,
                                    title: 'Chat ',
                                    ...props,
                                })
                            }
                        />

                        {/* Blog pages */}
                        <Route path='/blog' component={Blog} />

                        {/* Candidate pages */}
                        <Route path='/candidate' component={Candidate} />

                        {/* Candidate pages */}
                        <Route path='/employer' component={Employer} />

                        {/* Default 404 */}
                        <Route
                            render={(props) =>
                                withTitle({
                                    component: NotFound,
                                    title: '404 Error',
                                    ...props,
                                })
                            }
                        />
                    </Switch>
                </Suspense>
                {/* <Newsletter /> */}
                {/* <Footer /> */}
                {/* <Setting /> */}
            </StickyContainer>
        </Router>
    );
};


export default withRouter(App);

