async function get(url) {
    const config = {
        method: 'GET',
        headers: {
            'Content-Type': 'application/json',
            ...authHeader(url),
        },
    };
    return await axios
        .get(url, config)
        .then((response) => handleResponse(response))
        .catch((error) => {
            console.log('`fetch-wrapper.js` get(url) error: ', error);
        });
}

