import React from "react";
import { Link } from "react-router-dom";
import { Formik, Field, Form, ErrorMessage } from "formik";
import * as Yup from "yup";
import { accountService, alertService } from "../services";
import axios from "axios";
import { connect } from "react-redux";
import { login } from "src/redux/auth/thunks";

const baseUrl = `${"https://www.togedoortestgo.site/graphql"}/accounts`;

function Login({ history, location }) {
  const initialValues = {
    email: "",
    password: "",
  };

  function authHeader(url) {
    // return auth header with jwt if user is logged in and request is to the api url
    const user = accountService.userValue;
    const isLoggedIn = user && user.jwtToken;
    const isApiUrl = url.startsWith("https://www.togedoortestgo.site/graphql");
    if (isLoggedIn && isApiUrl) {
      return { Authorization: `Bearer ${user.jwtToken}` };
    } else {
      return {};
    }
  }

  const validationSchema = Yup.object().shape({
    email: Yup.string().email("Email is invalid").required("Email is required"),
    password: Yup.string().required("Password is required"),
  });

  function onSubmit({ email, password }, { setSubmitting }) {
    alertService.clear();

    const body = { email: email, password: password };
    var config1 = {
      headers: {
        "Content-Type": "application/json",
        ...authHeader(`${baseUrl}/authenticate`),
      },
    };

    axios
      .post(`${baseUrl}/authenticate`, body, config1)

      .then((res) => {
        console.log("after then");
        console.log(res);
        login(res);
        const { from } = location.state || { from: { pathname: "/" } };
        history.push(from);
      })
      .catch((error) => {
        setSubmitting(false);
        alertService.error(error);
      });
  }

  return (
    <Formik initialValues={initialValues} validationSchema={validationSchema} onSubmit={onSubmit}>
      {({ errors, touched, isSubmitting }) => (
        <Form>
          <h3 className='card-header'>Login</h3>
          <div className='card-body'>
            <div className='form-group'>
              <label>Email</label>
              <Field
                name='email'
                type='text'
                className={"form-control" + (errors.email && touched.email ? " is-invalid" : "")}
              />
              <ErrorMessage name='email' component='div' className='invalid-feedback' />
            </div>
            <div className='form-group'>
              <label>Password</label>
              <Field
                name='password'
                type='password'
                className={"form-control" + (errors.password && touched.password ? " is-invalid" : "")}
              />
              <ErrorMessage name='password' component='div' className='invalid-feedback' />
            </div>
            <div className='form-row'>
              <div className='form-group col'>
                <button type='submit' disabled={isSubmitting} className='btn btn-primary'>
                  {isSubmitting && <span className='spinner-border spinner-border-sm mr-1'></span>}
                  Login
                </button>
                <Link to='register' className='btn btn-link'>
                  Register
                </Link>
              </div>
              <div className='form-group col text-right'>
                <Link to='forgot-password' className='btn btn-link pr-0'>
                  Forgot Password?
                </Link>
              </div>
            </div>
          </div>
        </Form>
      )}
    </Formik>
  );
}

// export { Login };

// Login.propTypes = {
//     login: PropTypes.func.isRequired,
//     isAuthenticated: PropTypes.bool,
//   };

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
});

export default connect(mapStateToProps, { login })(Login);
