// External components
import React, { useEffect, useState } from 'react';
import { BehaviorSubject } from 'rxjs';
import { Dropdown, DropdownToggle, DropdownMenu, Media, NavItem } from 'reactstrap';
import { NavLink, Redirect } from 'react-router-dom';
import { faUserEdit, faCog, faSignInAlt, faSuitcase } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { accountService, alertService } from '../../../views/services';
import Menubar from '../MenuBar';
import store from '../../../store';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import axios from 'axios';

const ButtonGroups = ({ history }) => {
    const [dropdownOpen, setDropdownOpen] = useState(false);
    const token = localStorage.getItem('token');
    const [user, setUser] = useState({});

    var config2 = {
        headers: {
            'Authorization': token
        }
    }

    useEffect(() => {

        function fetchData() {
            axios.get(`https://www.togedoortestgo.site/graphql/accounts/${localStorage.id}`, config2)
                .then(res => {
                    setUser(res.data)
                })
        }
        fetchData();
        // useEffect(() => {
        //     accountService.getAll().then(x => setUsers(x));
    }, [token]);

    const toggle = () => setDropdownOpen((prevState) => !prevState);


    const handleLogout = () => {
        try {

            localStorage.removeItem('token');
            localStorage.removeItem('email');
            localStorage.removeItem('id');
            //google

            const logout = (res) => {
                console.log('out google');
                console.log(res);

            };
            const signOutgoogle = () => {
                const auth2 = window.gapi.auth2.getAuthInstance();
                if (auth2 != null) {
                    auth2.signOut().then(auth2.disconnect().then(logout));
                    // userSubject.next(null);

                } else {
                    this.props.onLogoutFailure();
                }
            };
            signOutgoogle();
            // end google

            localStorage.removeItem('token');
            localStorage.removeItem('email');
            localStorage.removeItem('id');

            // userSubject.next(null);


        } catch (error) {
            console.log(error);
        }
        setUser({})

    };



    return (
        <>
            <Dropdown
                className='user-profile d-xl-block d-lg-block d-md-block d-sm-block  d-none '
                isOpen={dropdownOpen}
                toggle={toggle}>
                <DropdownToggle className='bg-transparent text-dark border-0 p-0'>
                    <Media>
                        {user ? (
                            <Media left>
                                <Media
                                    object
                                    src={user.picture}
                                    alt="user profile"
                                    width="65px"
                                    style={{ marginTop: '10px' }}
                                    height="60px"
                                    className="rounded-circle"
                                />
                            </Media>
                        ) : (<p></p>)}

                        <Media
                            body
                            className='text-primary ml-3 d-none d-xl-block d-md-block d-lg-none'>
                            <Media heading tag="h5" className="text-dark">
                                {user.firstName} {user.lastName}
                            </Media>
                            {user.email}
                            {/* {authenticatedUser && authenticatedUser.role === 'User' ? (
                                <img src={temp} width='60' height='30' className='rounded-circle' />
                            ) : authenticatedUser && authenticatedUser.role === 'user from Google' ? (
                                <img
                                    src={`https://www.togedoortestgo.site/graphql/${user.picture}`}
                                    width='60'
                                    height='30'
                                    className='rounded-circle'
                                />
                            ) : (
                                <img width='60' height='30' className='rounded-circle' />
                            )} */}
                        </Media>
                    </Media>
                </DropdownToggle>
                <DropdownMenu className='p-0'>
                    <NavItem>
                        <NavLink className='nav-link single-link position-relative ' exact to='/candidate/profile'>
                            <FontAwesomeIcon className='mr-2 position-relative text-primary' icon={faUserEdit} />
                            Profile settings
                        </NavLink>
                    </NavItem>
                    <NavItem>
                        <a className='nav-link single-link position-relative ' href='/candidate/manage'>
                            <FontAwesomeIcon className=' mr-2 position-relative text-primary' icon={faSuitcase} />
                            My projects
                        </a>
                    </NavItem>
                    <NavItem>
                        <a className='nav-link single-link position-relative ' href='/' onClick={handleLogout}>
                            <FontAwesomeIcon
                                className=' mr-2 position-relative  text-primary'
                                icon={faSignInAlt}
                            />
                            Logout
                        </a>
                    </NavItem>
                </DropdownMenu>
            </Dropdown>
            <Menubar />
        </>
    );
};

// export default ButtonGroups
ButtonGroups.propTypes = {
    user: PropTypes.object.isRequired,
    isAuthenticated: PropTypes.bool,
};

const mapStateToProps = (state) => ({
    isAuthenticated: state.auth.isAuthenticated,
});

export default connect(mapStateToProps, {})(ButtonGroups);
