import React, { useState } from 'react';
import { BehaviorSubject } from 'rxjs';
import {
    Container,
    Row,
    Col,
    Form,
    FormGroup,
    Label,
    Input,
    Button,
    Card,
    InputGroup,
    InputGroupText,
    InputGroupAddon,
} from 'reactstrap';
import { faEnvelope, faLock } from '@fortawesome/free-solid-svg-icons';
import LoginItem from 'src/components/LoginItem';
import { Link, Redirect } from 'react-router-dom';
import { login } from '../../redux/auth/thunks';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { useForm, Controller } from 'react-hook-form';
import cls from './index.module.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { accountService, alertService } from "../../views/services";


const Signin = ({ login, isAuthenticated, ApiGoogle }) => {


    const {
        handleSubmit,
        control,
        formState: { errors },
    } = useForm();

    const [isAuthenticatedGoogle, setIsAuthenticatedGoogle] = useState(false);
    const [formData, setFormData] = useState({ email: '', password: '' });
    const { email, password } = formData;

    const onChange = (e, val, fn) => {
        setFormData({ ...formData, [e.target.name]: val });
        fn(val);
    };



    function onSubmit({ email, password },) {

        alertService.clear();
        login(email, password)
            .then(() => {
                const { from } = location.state || { from: { pathname: "/" } };
                history.push(from);
            })
            .catch(error => {
                alertService.error(error);
            });
    }



    if (isAuthenticatedGoogle) {
        return <Redirect to='/' />;
    }
    if (isAuthenticated) {
        return <Redirect to='/' />;
    }

    const responseGoogle = async (response) => {
        console.log(response);
        ApiGoogle(response);
    };

    const emailValidation = {
        rules: {
            required: true,
            pattern: {
                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                message: 'Invalid email address',
            },
        },
        error: errors.email && <p className={cls.errorMsg}>{errors.email.message}</p>,
    };

    const passValidation = {
        rules: {
            required: true,
            minLength: 6,
            message: 'This is the required field',
        },
        error: errors.password && <p className={cls.errorMsg}>This is the required field</p>,
    };

    return (
        <Container className='pt-13'>
            <Card className='overflow-hidden'>
                <Row>
                    <Col md={12} lg={6} className='p-0'>
                        <LoginItem setIsAuthenticatedGoogle={setIsAuthenticatedGoogle} />
                    </Col>
                    <Col md={12} lg={6} className='p-0'>
                        <Form className='text-center login-form  py-14 px-13' onSubmit={handleSubmit(onSubmit)}>
                            <h4 className='pb-5'>Login</h4>
                            <InputGroup className=' mb-4'>
                                <Controller
                                    control={control}
                                    name='email'
                                    rules={emailValidation.rules}
                                    render={({ field }) => (
                                        <Input
                                            className='bg-transparent text-primary border-right-0'
                                            name='email'
                                            type='email'
                                            inputplaceholder='Email Address*'
                                            placeholder='Email'
                                            autoComplete='off'
                                            onChange={(e) => onChange(e, e.target.value, field.onChange)}
                                        />
                                    )}
                                />
                                <InputGroupAddon addonType='append'>
                                    <InputGroupText className='bg-transparent text-primary border-left-0'>
                                        <FontAwesomeIcon className='text-primary' icon={faEnvelope} />
                                    </InputGroupText>
                                </InputGroupAddon>
                            </InputGroup>
                            {emailValidation.error}
                            {/*....................................................................... */}
                            <InputGroup className=' mb-4'>
                                <Controller
                                    control={control}
                                    name='password'
                                    rules={passValidation.rules}
                                    render={({ field }) => (
                                        <Input
                                            className='border-right-0'
                                            name='password'
                                            type='password'
                                            inputplaceholder='Password*'
                                            placeholder='Password'
                                            autoComplete='off'
                                            onChange={(e) => onChange(e, e.target.value, field.onChange)}
                                        />
                                    )}
                                />

                                <InputGroupAddon addonType='append'>
                                    <InputGroupText className='bg-transparent text-primary border-left-0'>
                                        <FontAwesomeIcon className='text-primary' icon={faLock} />
                                    </InputGroupText>
                                </InputGroupAddon>
                            </InputGroup>
                            {passValidation.error}

                            {/*....................................................................... */}
                            <FormGroup check className='my-5'>
                                <Label check className='text-muted'>
                                    <Input type='checkbox' /> Remember me? &nbsp;
                                    <Link to='/forget-Password' className='text-primary' id='forget-Password'>
                                        Forgot Password
                                    </Link>
                                </Label>
                            </FormGroup>
                            <Button block color='primary' className='btn-lg  my-5 outline-hover--effect'>
                                Login
                            </Button>
                            <FormGroup>
                                <Label className='text-muted'>
                                    Dont have an acount? &nbsp;
                                    <Link to='/account/register' className='text-primary'>
                                        Sign up
                                    </Link>
                                </Label>
                            </FormGroup>
                        </Form>
                    </Col>
                </Row>
            </Card>
        </Container>
    );
};

Signin.propTypes = {
    login: PropTypes.func.isRequired,
    isAuthenticated: PropTypes.bool,
};

const mapStateToProps = (state) => ({
    isAuthenticated: state.auth.isAuthenticated,
});
// export default Signin;
export default connect(mapStateToProps, { login })(Signin);
