import axios from 'axios';
import api from '../utils/api';

import {
    userLoaded,
    authError,
    loginSuccess,
    loginFail,
    editFail,
    editSuccess,
    editRequest,
} from './actions';



const token = localStorage.getItem('token');
// Load User
export const loadUser = () => async (dispatch) => {
    try {
        var config1 = {
            headers: {
                'Authorization': token
            }

        };
        const res = await api.get(`/${localStorage.id}`, config1);
        dispatch(userLoaded(res.data));

        console.log('success response:_', localStorage.id);
    } catch (err) {
        dispatch(authError());
        // console.log('This Error from loadUser in `thunks.js`:');
        throw err;
    }
};

