// Login User
export const login = (email, password) => async (dispatch) => {
    console.log('redux log func');
    const body = { email, password };
    console.log('body');
    console.log(body);
    try {
        const res = await api.post('/authenticate', body);
        console.log('LOGIN SUCCESS');
        console.log('  reedux res');
        console.log(' From login response');
        console.log(res.data);
        dispatch(loginSuccess(res.data));
        dispatch(loadUser());
    } catch (err) {
        const errors = err.response.data.errors;

        if (errors) {
            console.log('error', errors);
        }

        dispatch(loginFail());
    }
};

export const ApiGoogle = (response) => async (dispatch) => {
    console.log('new res google');

    axios
        .post(' https://www.togedoortestgo.site/graphql/services/googlelogin', { tokenId: response.tokenId })
        .then((res) => {
            const response = res.data;
            console.log(' From google response');
            console.log(res);
            console.log(response);
            // localStorage.setItem("token", response.token);
            // window.location.reload(false);
            // debugger
            if (response) {
                console.log('loaduser() in google');
                dispatch(loginSuccess(response));
                dispatch(loadUser());
            } else {
                dispatch(loginFail());
            }
        })
        .catch((error) => {
            dispatch(loginFail());
            console.log(error);
        });
};

export const updateUser = (userID, formData) => async (dispatch) => {
    console.log(userID, formData)
    dispatch(editRequest());
    try {
        api.patch(`/${userID}`, formData);
        console.log('UPDATE SUCCESSS------------------------------------');
        dispatch(editSuccess(formData));
        dispatch(loadUser);
    } catch (err) {
        dispatch(editFail());
    }
};

